import React, { Component, PropTypes } from 'react'

class ProjectStep extends React.Component {
  render() {
    return (
      <div>
        <p>Mon projet</p>
        <fieldset className="form-group">
          <label>Nom de mon projet</label>
          <input type="text" className="form-control" placeholder="projet"/>
          <small className="text-muted">Le nom n'est pas obligatoire.</small>
        </fieldset>
        <fieldset className="form-group">
          <label>Résumé de mon projet</label>
          <textarea rows="5" className="form-control"/>
          <small className="text-muted">Non confidientiel, ne devant pas comporter d'éléments senssibles</small>
        </fieldset>
        <fieldset className="form-group">
          <label>Type de projet</label>
          <select defaultValue="0" className="form-control">
            <option value="0" disabled>Selectionner</option>
            <option value="1">Auto-entrepreneuriat ou microactivité</option>
            <option value="2">Création d'entreprise</option>
            <option value="3">Reprise d'entreprise</option>
            <option value="4">Création ou transmission d'une autre forme d'activité (association et autre)</option>
            <option >Création ou transmission d'entreprise familiale</option>
          </select>
        </fieldset>
        <fieldset className="form-group">
          <label>Etat d'avancement</label>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
               Construction
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
              Lancement
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
             Création, reprise ou transmission réalisée
            </label>
          </div>
        </fieldset>
      </div>
    )
  }
}

export default ProjectStep



