import React, { Component, PropTypes } from 'react'

const divStyle = {
  marginBottom: '10px'
}

class TeamStep extends React.Component {
  render() {
    return (
      <div style={divStyle}>
        <p>Mon équipe</p>
        <div className="checkbox">
          <label>
            <input type="checkbox"/>
            Je fais mon projet en équipe
          </label>
        </div>
        <fieldset className="form-group">
          <label>Nom</label>
          <input type="text" className="form-control"/>
        </fieldset>
        <fieldset className="form-group">
          <label>Prénom</label>
          <input type="text" className="form-control"/>
        </fieldset>
        <fieldset className="form-group">
          <label>Rôle dans le projet</label>
          <input type="email" className="form-control"/>
        </fieldset>
        <div className="checkbox">
          <label>
            <input type="checkbox"/>
            Candidate également au statut
          </label>
        </div>
        <button type="button" className="btn btn-primary">Ajouter</button>
      </div>
    )
  }
}

export default TeamStep



