import React, { Component, PropTypes } from 'react'

class ContextStep extends React.Component {
  render() {
    return (
      <div>
        <p>Ma situation</p>
        <fieldset className="form-group">
          <label>En septembre 2016 :</label>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
               J'ai aurai fini ou arrêté mes études
            </label>
          </div>
          <div className="radio">
            <label>
              <input type="radio" name="progressRadio"/>
              Je serai étudiant·e
            </label>
          </div>
          <small>Ne pas prendre en compte l'inscription au diplôme étudiant·e entrepreneur·e (D2E)</small>
        </fieldset>
        <p>Ici, bientôt l'identité, les coordonnées...</p>
      </div>
    )
  }
}

export default ContextStep



