import React, { Component, PropTypes } from 'react'
import ProjectStep from './ProjectStep'
import InformationStep from './InformationStep'
import ContextStep from './ContextStep'
import TeamStep from './TeamStep'
import PepiteStep from './PepiteStep'
import PathStep from './PathStep'

const Steps =
  [
    { name: 'Informations', component: <InformationStep/> },
    { name: 'Mon Projet', component: <ProjectStep/> },
    { name: 'Mon Equipe', component: <TeamStep/> },
    { name: 'Ma Situation', component: <ContextStep/> },
    { name: 'Mon Parcours', component: <PathStep/> },
    { name: 'Mon PEPITE', component: <PepiteStep/> }
  ]

export default Steps



